import React, { useEffect, useState } from 'react'
import { PersonAddOutlined,PersonRemoveOutlined } from '@mui/icons-material'
import { Box,IconButton,Typography,useTheme } from '@mui/material'
import { useDispatch,useSelector } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import { setConnection } from 'state'
import FlexBetween from './FlexBetween'
import UserImage from './UserImage'
import CircleIcon from '@mui/icons-material/Circle';

export default function Connection({connectionId,name,showConnect=false,time=null,profilePicture=null}) {
    const {palette} = useTheme()
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const token = useSelector(state => state.token)
    const {_id} = useSelector(state => state.user)
    const connections = useSelector(state => state.user.connections)
    const primaryLight = palette.primary.light
    const primaryDark = palette.primary.dark
    const main = palette.neutral.main
    const medium = palette.neutral.medium

    // const [profileImg,setProfileImg]=useState("data:image/png;base64,")

    const isConnection=connections.find(connection=>connection._id===connectionId)
    
    const patchConnection = async (method) => {
        //console.log("patch connection pressed")
        const response = await fetch(`${process.env.REACT_APP_BASE_URL}/user/${_id}/${connectionId}`, {
            method:"PATCH",
            headers: { 
                Authorization: `Bearer ${token}`,
                "Content-Type": "application/json"
            },
        })
        const data = await response.json()
        dispatch(setConnection({ connections: data }))
    }

    // const getUser=async()=>{
    //     const response =await fetch(`${process.env.REACT_APP_BASE_URL}/user/${connectionId}`,{
    //         method:"GET",
    //         headers:{Authorization: `Bearer ${token}`}
    //     })
    //     const data=await response.json()
    //     setProfileImg(data.profilePicture)
    // }

    // useEffect(()=>{
    //     getUser()
    // },[connectionId])

  return (
    <FlexBetween>
        <FlexBetween gap="1rem">
            <UserImage image={profilePicture}/>
            <Box
                onClick={() =>{
                    navigate(`/profile/${connectionId}`)
                    // navigate(0)
                }}
            >
                <Typography
                    color={main}
                    variant='h5'
                    fontWeight="500"
                    sx={{
                        "&:hover":{
                            cursor:"pointer",
                            // color:palette.primary.light
                        }
                    }}
                >
                    {name}
                </Typography>
                {time&&<Typography color={palette.neutral.medium}>{time}</Typography>}
            </Box>
        </FlexBetween>
        {connectionId!==_id&&showConnect&&<IconButton
            onClick={() => patchConnection()}
            sx={{ backgroundColor: palette.primary.light,p:"0.6rem" }}
        >
            {isConnection ? (
                <PersonRemoveOutlined sx={{ color: primaryDark }} />
            ) : (
                <PersonAddOutlined sx={{ color: primaryDark }} />
            )}
        </IconButton>}
    </FlexBetween>
  )
}
